
import Actionsheet from 'vux/src/components/actionsheet/index.vue'


export default {
name: 'AddMeet',
components: {
Actionsheet
},
data () {
return {
show1: false,
menus1: ['会议室1', '会议室2', '会议室3']
}
},
methods: {
handleSelectRoom () {
this.show1 = true
},
handlePickRoom (index) {
this.roomValue = this.menus1[index]
},
handleSelectTime () {
this.$router.push(`/selectTime`)
},
handleSelectPerson () {
this.$router.push(`/selectPerson`)
}
}

}



